@import "../../global.scss";

.portfolio {
  background-color: #d2e0fb;
  display: flex;
  flex-direction: column;
  align-items: center;
  h1 {
    margin: 10px;
    padding: 0;
    @include mobile {
      font-size: 20px;
    }
  }
  ul {
    margin: 10px;
    list-style: none;
    padding: 0;
    display: flex;
    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
    li {
      font-size: 16px;
      margin-right: 5px;
      padding: 7px;
      border-radius: 10px;
      cursor: pointer;
      &.active {
        background-color: $mainColor;
        color: white;
      }
    }
  }
  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    @include mobile {
      width: 100%;
    }
    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid lightgray;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $mainColor;
      position: relative;
      transition: all 0.4s ease;
      cursor: pointer;
      @include mobile {
        // height: 100px;
        // width: 130px;
        margin: 0;
      }
      h3 {
        position: absolute;
        font-style: 20px;
      }
      img {
        width: 100%;
        height: 75%;
        object-fit: cover;
        z-index: 1;
      }
      &:hover {
        background-color: $mainColor;
        color: white;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
