@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;
  @include mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .left {
    flex: 0.5;
    overflow: hidden;
    display: flex;
    .imgContainer {
      margin-top: 25px;
      width: 700px;
      height: 115%;
      background-color: crimson;
      border-radius: 50%;
      align-items: flex-end;
      justify-content: center;
      float: right;
      @include mobile {
        align-items: flex-start;
      }
      img {
        width: 100%;
        @include mobile {
          width: 50%;
          margin-left: 20%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;
    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @include mobile {
        padding-left: 0;
        align-items: center;
      }
      h1 {
        font-size: 60px;
        @include mobile {
          font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        @include mobile {
          font-size: 20px;
        }
        font-size: 30px;
        span {
          font-size: inherit;
          color: crimson;
        }
        .ityped-cursor {
          animation: blink 1s infinite;
        }
        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 50%;
      img {
        width: 30px;
        animation: arrowBlink 2s infinite;
        @keyframes arrowBlink {
          100% {
            opacity: 0;
          }
        }
      }
    }
  }
}
