@import "../../global.scss";

.topbar {
  background-color: white;
  color: $mainColor;
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;
  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      .logo {
        text-decoration: none;
        color: inherit;
        font-size: 40px;
        font-weight: 700;
      }
      .itemContainer {
        @include mobile {
          display: none;
        }
        display: flex;
        align-items: center;
        margin-left: 20px;
        .icon {
          font-size: 18px;
          margin-right: 5px;
          margin-left: 30px;
        }
        span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }
    .right {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        span {
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 1s ease;
        }
      }
    }
  }
  &.active {
    background-color: $mainColor;
    color: white;
    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          transform: rotate(-45deg);
          background-color: white;
        }
      }
    }
  }
}
